/* FONT FACE */
/* FONT FACE END*/
/* ========== HEADER SECTION ========== */
html {
  font-size: 62.5%;
  color: #FFFFFF;
  height: 100%;
  box-sizing: border-box;
}

body {
  background: #dadcdf;
}

*, *:before, *:after {
  box-sizing: inherit;
}

.wrapper {
  max-width: 600px;
  margin: 0 auto;
}

.visuallyhidden {
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  white-space: nowrap;
}

.header {
  font-family: "Rancho", cursive;
  font-size: 5rem;
  color: #16A1A4;
  line-height: 5rem;
  font-weight: 400;
  text-align: center;
  padding: 40px;
}

/* ========== MAIN SECTION ========== */
.userCity {
  text-align: center;
  margin-bottom: 60px;
}

.userCity__input {
  font-family: "Kameron", serif;
  font-size: 2rem;
  color: #000000;
  line-height: 2rem;
  font-weight: 400;
  width: 80%;
  padding: 10px 30px;
  background: #dadcdf;
  border: 0;
  border-bottom: 1px solid #3c454e;
  transition: all 0.2s linear;
}

.userCity__input::placeholder {
  text-align: center;
}

.userCity__input:hover, .userCity__input:focus {
  width: 85%;
  background-color: rgba(204, 204, 204, 0.6);
  color: #000000;
  outline: 2px solid #16A1A4;
  border-radius: 0;
}

.userCity__button {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  color: #000000;
  line-height: 2.5rem;
  font-weight: 400;
  width: 80%;
  letter-spacing: 1px;
  padding: 10px 30px;
  text-transform: uppercase;
  color: #FFFFFF;
  background-color: #16A1A4;
  border: 0;
  margin-top: 20px;
  border-radius: 25px;
  transition: all 0.2s linear;
}

.userCity__button:hover, .userCity__button:focus {
  width: 85%;
  background-color: rgba(204, 204, 204, 0.6);
  color: #000000;
  outline: 2px solid #16A1A4;
  border-radius: 0;
}

.main {
  font-family: "Kameron", serif;
  font-size: 4rem;
  color: #FFFFFF;
  line-height: 4rem;
  font-weight: 400;
  display: flex;
  flex-direction: column;
}

.weather {
  font-family: "Kameron", serif;
  font-size: 4rem;
  color: #FFFFFF;
  line-height: 4rem;
  font-weight: 400;
  position: relative;
  background-color: #16A1A4;
  flex-basis: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  border-radius: 25px 25px 0 0;
}

.weather-add {
  flex-basis: 100%;
  display: flex;
  flex-direction: row;
}

.weather__humidity, .weather__wind {
  background-color: #3A434C;
  flex-basis: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.weather__humidity {
  border-bottom-left-radius: 25px;
}

.weather__wind {
  border-bottom-right-radius: 25px;
}

.weather__icon, .weather__description, .weather__city {
  flex-basis: 100%;
  text-align: center;
}

.weather__temp {
  font-size: 4rem;
  position: absolute;
  top: 20px;
  left: 20px;
}

.weather__icon {
  font-size: 10rem;
}

.weather__description {
  font-size: 3rem;
}

.weather__city {
  font-size: 2rem;
}

/* ========== FOOTER SECTION ========== */
.page-footer {
  font-family: "Open Sans", sans-serif;
  font-size: 1.2rem;
  color: #595959;
  line-height: 2.5rem;
  font-weight: 300;
  background-color: #171717;
  position: absolute;
  bottom: 0;
  width: 100%;
}

.page-footer .wrapper {
  height: 60px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.page-footer a {
  text-decoration: none;
  color: inherit;
  transition: all 0.2s linear;
}

.page-footer a:hover {
  padding-bottom: 3px;
  border-bottom: 2px solid #1e8200;
  color: #1e8200;
}

.page-footer a:focus {
  outline: 2px solid #1e8200;
}

